.fa-icon {
  display: inline-block;
  fill: currentColor;
  overflow: visible;
  vertical-align: -0.125em;
}
.fa-icon > g {
  transform-origin: 50% 50%;
}
.fa-flip-horizontal {
  transform: scale(-1, 1);
}
.fa-flip-vertical {
  transform: scale(1, -1);
}
.fa-flip-both {
  transform: scale(-1, -1);
}
.fa-spin > g {
  animation: fa-spin 1s 0s infinite linear;
}
.fa-pulse > g {
  animation: fa-spin 1s infinite steps(8);
}
.fa-inverse {
  color: #fff;
}
@keyframes fa-spin {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
